/* eslint-disable react/prop-types */
import Button from "@mui/material/Button";
import { useState } from "react";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import { useDispatch } from "react-redux";
import { createSession, startSession } from "../state/currentSessionReducer";
import sessions from "./sessions";
import "./StartCurrentSession.css";
import uuid from "../utils/uuid";
import { useNavigate } from "react-router";
import { Box, Grid, Typography } from "@mui/material";

function StartItem({ Icon, label, onClick, onCreate, starting }) {
  return (
    <Grid item>
      <Box sx={{ display: "flex", flexDirection: "row", gap: 2 }}>
        <Box sx={{ maxWidth: "5em" }}>
          <Icon sx={{ width: 1, height: 1 }} />
        </Box>
        <Box sx={{ display: "flex", flexDirection: "column" }}>
          <Typography variant="h3">{label}</Typography>
          {!starting && (
            <Button onClick={onClick} color="secondary">
              Start new session
            </Button>
          )}
          {!starting && (
            <Button onClick={onCreate} color="secondary">
              Create new session
            </Button>
          )}
          {starting && (
            <Button
              onClick={onClick}
              startIcon={<PlayArrowIcon />}
              color="primary"
            >
              Ok! Click and Let&apos;s go
            </Button>
          )}
        </Box>
      </Box>
    </Grid>
  );
}

export default function StartCurrentSession() {
  const [startingItem, setStartingItem] = useState();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  return (
    <Grid
      container
      spacing={5}
      direction="row"
      justifyContent="center"
      alignItems="center"
      padding={4}
    >
      {Object.entries(sessions).map(([key, { Icon, label }]) => (
        <StartItem
          key={key}
          Icon={Icon}
          label={label}
          onClick={() =>
            startingItem === key
              ? dispatch(startSession(key))
              : setStartingItem(key)
          }
          onCreate={() => {
            const id = uuid();
            dispatch(createSession({ id, type: key }));
            navigate(`/sessions/${id}`);
          }}
          starting={startingItem === key}
        />
      ))}
    </Grid>
  );
}
