import React from "react";
import { useSelector } from "react-redux";
import { Navigate } from "react-router";
import { existRunningSessionSelector } from "../state/currentSessionReducer";
import ApplicationBar from "../componets/ApplicationBar";
import CurrentSessionComp from "../componets/CorrentSession";

/**
 *
 */
export function CurrentSession() {
  const existRunningSession = useSelector(existRunningSessionSelector);

  if (!existRunningSession) {
    return <Navigate to="/" />;
  }

  return (
    <React.Fragment>
      <ApplicationBar />
      {existRunningSession && <CurrentSessionComp />}
    </React.Fragment>
  );
}
