/* eslint-disable no-unused-vars */
/* eslint-disable no-unused-expressions */
import { createSlice } from "@reduxjs/toolkit";
import { calcDuration } from "../domain/sessions";
import moment from "moment";

const initialState = {
  sessions: [],
  loading: true,
};

function changeSession(id, state, func) {
  const session = state.sessions.find((s) => s.id === id);
  if (session) {
    func(session);
    state.sessions = [...state.sessions];
  }
}

const sessionsSlice = createSlice({
  name: "sessions",
  initialState,
  reducers: {
    setSessions(state, action) {
      state.sessions = [...action.payload];
      state.loading = false;
    },

    addSession(state, action) {
      state.sessions = [...state.sessions, action.payload];
    },

    changeDate(state, action) {
      const { id, date } = action.payload;
      changeSession(id, state, (session) => {
        const duration = calcDuration(session.logs);
        const mdate = moment(date);
        session.logs = [
          {
            start: date,
            stop: mdate
              .add(duration.asMilliseconds(), "milliseconds")
              .toISOString(),
          },
        ];
      });
    },

    changeDuration(state, action) {
      const { id, duration } = action.payload;
      changeSession(id, state, (session) => {
        const mdate = moment(session.logs[0].start);
        session.logs = [
          {
            start: session.logs[0].start,
            stop: mdate.add(duration, "milliseconds").toISOString(),
          },
        ];
      });
    },

    changePlace(state, action) {
      const { id, place } = action.payload;
      changeSession(id, state, (session) => {
        session.place = place;
      });
    },

    changeLinkPlace(state, action) {
      const { id, linkPlace } = action.payload;
      changeSession(id, state, (session) => {
        session.linkPlace = linkPlace;
      });
    },

    changeDistance(state, action) {
      const { id, distance } = action.payload;
      changeSession(id, state, (session) => {
        session.distance = distance;
      });
    },

    setLogs(state, action) {
      const { id, logs } = action.payload;
      const session = state.sessions.find((s) => s.id === id);
      if (session) {
        session.logs = logs;
        state.sessions = [...state.sessions];
      }
    },
  },
});

export const sessionsSelector = (state) =>
  state.sessions.loading ? [] : state.sessions.sessions;

export const sessionSelector = (id) => (state) =>
  state.sessions.sessions.find((s) => s.id === id);

export const {
  setSessions,
  addSession,
  changeDate,
  changeDuration,
  setLogs,
  changeLinkPlace,
  changePlace,
  changeDistance,
} = sessionsSlice.actions;
export default sessionsSlice.reducer;
