import React from "react";
import { useSelector } from "react-redux";
import { Navigate } from "react-router";
import StartCurrentSession from "../componets/StartCurrentSession";
import Sessions from "../componets/Sessions.jsx";
import { existRunningSessionSelector } from "../state/currentSessionReducer";
import ApplicationBar from "../componets/ApplicationBar";

/**
 * Pagina di presentazione e caricamento dello stato.
 */
export function Home() {
  const existRunningSession = useSelector(existRunningSessionSelector);

  if (existRunningSession) {
    return <Navigate to="/current-session" />;
  }
  return (
    <React.Fragment>
      <ApplicationBar />
      <StartCurrentSession />
      <Sessions />
    </React.Fragment>
  );
}
