import { useMemo } from "react";
import { useSelector } from "react-redux";
import { sessionsSelector } from "../state/sessionsReducer";
import uisessions from "./sessions.js";
import {
  calcMinPerKm,
  calcSpeed,
  getSessionDetail,
  parseDuration,
} from "../domain/sessions";
import {
  Avatar,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
} from "@mui/material";
import { dateToString } from "../utils/date";
import { useNavigate } from "react-router";
import { Box } from "@mui/system";

export default function Sessions() {
  const sessions = useSelector(sessionsSelector);
  const navigate = useNavigate();

  const sessionDetails = useMemo(
    () =>
      sessions
        .map(getSessionDetail)
        .sort((d1, d2) => (d1.date > d2.date ? -1 : 1)),
    [sessions]
  );
  return (
    <Box sx={{ m: 2, p: 2 }}>
      <Typography variant="h5" gutterBottom>
        Sessions
      </Typography>
      <List sx={{ bgcolor: "background.paper" }}>
        {sessionDetails.map((session) => {
          const Icon = uisessions[session.type].Icon;
          const title = `${dateToString(session.date)} ${
            uisessions[session.type].label
          }`;
          const subtitle = (
            <Box>
              {`Duration: ${session.duration}, `}
              {session.distance ? `Distance: ${session.distance} Km, ` : ""}
              {session.distance && session.type === "Running"
                ? `Speed: ${calcSpeed(
                    parseDuration(session.duration),
                    session.distance
                  ).toFixed(1)} Km/h, ${calcMinPerKm(
                    parseDuration(session.duration),
                    session.distance
                  )} min/Km, `
                : ""}
              {`Place: `}
              {session.linkPlace ? (
                <a
                  href={`http://${session.linkPlace}`}
                  target="_blank"
                  rel="noreferrer"
                  onClick={(event) => event.stopPropagation()}
                >
                  {session.place}
                </a>
              ) : (
                session.place
              )}
            </Box>
          );
          return (
            <ListItem key={session.id}>
              <ListItemAvatar
                className="sessions__item_avatar"
                onClick={() => navigate(`/sessions/${session.id}`)}
              >
                <Avatar>
                  <Icon />
                </Avatar>
              </ListItemAvatar>
              <ListItemText primary={title} secondary={subtitle} />
            </ListItem>
          );
        })}
      </List>
      {sessionDetails.length === 0 && (
        <Typography variant="subtitle1">No sessions found!</Typography>
      )}
    </Box>
  );
}
