/* eslint-disable react/prop-types */
import { Box, Button, Typography } from "@mui/material";
import React, { useCallback, useEffect, useState } from "react";
import sessions from "./sessions";
import PauseIcon from "@mui/icons-material/Pause";
import StopIcon from "@mui/icons-material/Stop";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import { useDispatch, useSelector } from "react-redux";
import {
  currentSessionSelector,
  pauseSession,
  resumeSession,
  stopSession,
} from "../state/currentSessionReducer";
import { calcDuration, durationToString } from "../domain/sessions";
import { useNavigate } from "react-router";

function Item({
  Icon,
  label,
  duration,
  state,
  onTerminate,
  onPause,
  onResume,
}) {
  return (
    <React.Fragment>
      <Box sx={{ maxWidth: "5em" }}>
        <Icon sx={{ width: 1, height: 1 }} />
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Typography variant="h3">
          {`${label} ${durationToString(duration)}`}
        </Typography>
        <Box>
          {state === "running" && (
            <Button color="primary" onClick={onPause} startIcon={<PauseIcon />}>
              Pause
            </Button>
          )}
          {state === "paused" && (
            <Button
              color="primary"
              onClick={onResume}
              startIcon={<PlayArrowIcon />}
            >
              Resume
            </Button>
          )}
          {state === "paused" && (
            <Button
              color="secondary"
              onClick={onTerminate}
              startIcon={<StopIcon />}
            >
              Terminate
            </Button>
          )}
        </Box>
      </Box>
    </React.Fragment>
  );
}

export default function CorrentSession() {
  const { id, type, logs, state } = useSelector(currentSessionSelector);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [duration, setDuration] = useState(calcDuration(logs));
  useEffect(() => {
    if (state === "running") {
      const interval = setInterval(() => {
        setDuration(calcDuration(logs));
      }, 1000);
      return () => clearInterval(interval);
    }
    return undefined;
  }, [state, logs]);
  const { Icon, label } = sessions[type];

  const handleStop = useCallback(() => {
    dispatch(stopSession());
    navigate(`/sessions/${id}`);
  }, [dispatch, navigate, id]);

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        gap: 2,
        p: 2,
        m: 2,
      }}
    >
      <Item
        Icon={Icon}
        label={label}
        onTerminate={handleStop}
        onPause={() => dispatch(pauseSession())}
        onResume={() => dispatch(resumeSession())}
        duration={duration}
        state={state}
      />
    </Box>
  );
}
