import { configureStore } from "@reduxjs/toolkit";
import currentSessionReducer, {
  reset,
  setCurrentSession,
} from "./currentSessionReducer";
import sessionsReducer, { addSession, setSessions } from "./sessionsReducer";
import {
  saveSessions as saveSessionsToStorage,
  getSessions,
} from "../persistence/sessions";
import {
  getSession,
  saveSession as saveSessionToStorage,
} from "../persistence/currentSession";

const store = configureStore({
  reducer: {
    currentSession: currentSessionReducer,
    sessions: sessionsReducer,
  },
});

function addStoppedSession() {
  if (store.getState().currentSession.state === "stopped") {
    const session = store.getState().currentSession;
    store.dispatch(reset());
    store.dispatch(addSession(session));
  }
}

let lastSessions = undefined;
function saveSessions() {
  const sessions = store.getState().sessions.sessions;
  if (lastSessions !== sessions) {
    saveSessionsToStorage(sessions);
    lastSessions = sessions;
  }
}

let lastSession = undefined;
function saveSession() {
  const session = store.getState().currentSession;
  if (lastSession !== session) {
    saveSessionToStorage(session);
    lastSessions = session;
  }
}

export function initialize() {
  store.dispatch(setSessions(getSessions()));
  store.dispatch(setCurrentSession(getSession()));

  store.subscribe(() => addStoppedSession());
  store.subscribe(() => saveSessions());
  store.subscribe(() => saveSession());
}

export default store;
