import { stringify } from "csv-stringify/browser/esm";
import moment from "moment";

const sessions = Object.freeze({
  Trekking: {
    label: "Trekking",
  },
  Running: {
    label: "Running",
  },
  Climbing: {
    label: "Climbing",
  },
  Fitness: {
    label: "Fitness",
  },
});

export function durationToString(duration) {
  const hours = String(duration.hours()).padStart(2, "0");
  const minutes = String(duration.minutes()).padStart(2, "0");
  const seconds = String(duration.seconds()).padStart(2, "0");
  return `${hours}:${minutes}:${seconds}`;
}

export function parseDuration(duration) {
  try {
    const [hours, minutes, seconds] = duration.split(":");
    return moment.duration({
      seconds: Number((seconds || "0").trim()),
      minutes: Number((minutes || "0").trim()),
      hours: Number((hours || "0").trim()),
    });
  } catch (e) {
    // parsing error
    return null;
  }
}

export function calcDuration(logs) {
  const mills = logs.reduce((sum, log) => {
    return sum + moment(log.stop || new Date()).diff(moment(log.start));
  }, 0);
  return moment.duration(mills);
}

export function calcSpeed(duration, distance) {
  if (!duration || !distance) return 0;
  return (3600 * 1000 * distance) / duration.asMilliseconds();
}

export function calcMinPerKm(duration, distance) {
  if (!duration || !distance) return 0;
  const value = duration.asMilliseconds() / (60 * 1000) / distance;
  const minutes = Math.floor(value);
  const seconds = Math.floor((value - Math.floor(value)) * 60);
  return `${String(minutes).padStart(2, "0")}:${String(seconds).padStart(
    2,
    "0"
  )}`;
}

export function getSessionDetail(session) {
  return {
    ...session,
    date: session.logs[0].start,
    duration: durationToString(calcDuration(session.logs)),
  };
}

export async function getCVSSessions(sessions) {
  return new Promise((resolve, reject) => {
    const detail = sessions.map(getSessionDetail);
    stringify(detail, { header: true }, (err, data) => {
      if (err) {
        reject(err);
      } else {
        resolve(data);
      }
    });
  });
}

export default sessions;
