/* eslint-disable no-unused-vars */
/* eslint-disable no-unused-expressions */
import { createSlice } from "@reduxjs/toolkit";
import uuid from "../utils/uuid";

const initialState = {
  logs: [],
  type: undefined,
  state: "undefined",
  description: undefined,
};

const currentSessionSlice = createSlice({
  name: "currentSession",
  initialState,
  reducers: {
    createSession(state, action) {
      state.id = action.payload.id;
      state.type = action.payload.type;
      state.state = "stopped";
      state.logs = [
        { start: new Date().toISOString(), stop: new Date().toISOString() },
      ];
    },

    startSession(state, action) {
      state.id = uuid();
      state.type = action.payload;
      state.state = "running";
      state.logs = [{ start: new Date().toISOString(), stop: undefined }];
    },

    pauseSession(state) {
      state.state = "paused";
      const lastLog = state.logs[state.logs.length - 1];
      lastLog.stop = new Date().toISOString();
    },

    resumeSession(state) {
      state.state = "running";
      state.logs = [
        ...state.logs,
        { start: new Date().toISOString(), stop: undefined },
      ];
    },

    stopSession(state) {
      if (state.state === "running") {
        const lastLog = state.logs[state.logs.length - 1];
        lastLog.stop = new Date().toISOString();
      }
      state.state = "stopped";
    },

    reset(state) {
      state.state = initialState.state;
      state.logs = initialState.logs;
      state.type = initialState.type;
      state.description = initialState.description;
    },

    setCurrentSession(state, action) {
      if (action.payload) {
        state.state = action.payload.state;
        state.logs = action.payload.logs;
        state.type = action.payload.type;
        state.description = action.payload.description;
      }
    },
  },
});

export const existRunningSessionSelector = (state) =>
  state.currentSession.state === "running" ||
  state.currentSession.state === "paused";

export const currentSessionTypeSelector = (state) => state.currentSession.type;

export const currentSessionSelector = (state) => ({
  id: state.currentSession.id,
  logs: state.currentSession.logs.map((log) => ({
    start: log.start && new Date(log.start),
    stop: log.stop && new Date(log.stop),
  })),
  type: state.currentSession.type,
  state: state.currentSession.state,
  description: state.currentSession.description,
});

export const {
  createSession,
  startSession,
  pauseSession,
  resumeSession,
  stopSession,
  reset,
  setCurrentSession,
} = currentSessionSlice.actions;
export default currentSessionSlice.reducer;
