/* eslint-disable react/prop-types */
import { Box, Button, Typography } from "@mui/material";
import FileSaver from "file-saver";
import { useDispatch } from "react-redux";
import { getCVSSessions } from "../domain/sessions.js";
import { getSessions } from "../persistence/sessions.js";
import { setSessions } from "../state/sessionsReducer.js";
import Screen from "./Screen.jsx";

function Operation({ title, subtitle, action }) {
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
        border: 1,
        borderRadius: 1,
        p: 2,
      }}
    >
      <Box>
        <Typography variant="h6" gutterBottom>
          {title}
        </Typography>
        <span>{subtitle}</span>
      </Box>
      <Box>{action}</Box>
    </Box>
  );
}

function downloadSessions() {
  FileSaver.saveAs(
    new Blob([JSON.stringify(getSessions())], {
      type: "text/json;charset=utf-8",
    }),
    "sessions.json"
  );
}

function downloadCsvSessions() {
  getCVSSessions(getSessions()).then((data) => {
    FileSaver.saveAs(
      new Blob([data], {
        type: "text/csv;charset=utf-8",
      }),
      "sessions.csv"
    );
  });
}

async function uploadSessions(file, dispatch) {
  const text = await file.text();
  dispatch(setSessions(JSON.parse(text)));
}

/**
 * Pagina configurazione dell'applicazione.
 */
export function Settings() {
  const dispatch = useDispatch();
  return (
    <Screen>
      <Typography variant="h5" gutterBottom>
        Import/Export
      </Typography>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          gap: 2,
          m: 1,
        }}
      >
        <Operation
          title="Download Sessions"
          subtitle="Download all sessions in JSON format"
          action={
            <Button
              color="secondary"
              onClick={downloadSessions}
              component="label"
            >
              Download
            </Button>
          }
        />
        <Operation
          title="Upload Sessions"
          subtitle="Upload all sessions"
          action={
            <Button color="secondary" component="label">
              Upload
              <input
                type="file"
                hidden
                onChange={(event) =>
                  uploadSessions(event.target.files.item(0), dispatch)
                }
              />
            </Button>
          }
        />
        <Operation
          title="Download Sessions CVS"
          subtitle="Download all sessions in CVS format"
          action={
            <Button
              color="secondary"
              onClick={downloadCsvSessions}
              component="label"
            >
              Download
            </Button>
          }
        />
      </Box>
    </Screen>
  );
}
