import {
  Autocomplete,
  Box,
  Button,
  TextField,
  Typography,
} from "@mui/material";
import { DatePicker } from "@mui/lab";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import sessions from "../componets/sessions";
import {
  changeDate,
  changeDistance,
  changeDuration,
  changeLinkPlace,
  changePlace,
  sessionSelector,
  setLogs,
} from "../state/sessionsReducer";
import Screen from "./Screen";
import { useCallback, useState } from "react";
import { getSessionDetail, parseDuration } from "../domain/sessions";
import { debounce } from "../utils/js";
import useStateOrValue from "../componets/useStateOrValue";

const setDuration = debounce((value, id, logs, dispatch, appendAction) => {
  const duration = parseDuration(value);
  if (duration) {
    dispatch(
      changeDuration({
        id,
        duration: duration.asMilliseconds(),
      })
    );
    appendAction(() => dispatch(setLogs({ id, logs })));
  }
}, 1000);

const setPlace = debounce((place, id, lastPlace, dispatch, appendAction) => {
  dispatch(changePlace({ id, place }));
  appendAction(() => dispatch(changePlace({ id, place: lastPlace })));
}, 1000);

const setPlaceLink = debounce(
  (linkPlace, id, lastLinkPlace, dispatch, appendAction) => {
    dispatch(changeLinkPlace({ id, linkPlace }));
    appendAction(() =>
      dispatch(changeLinkPlace({ id, linkPlace: lastLinkPlace }))
    );
  },
  1000
);
const setDistance = debounce(
  (distance, id, lastDistance, dispatch, appendAction) => {
    dispatch(changeDistance({ id, distance }));
    appendAction(() =>
      dispatch(changeDistance({ id, distance: lastDistance }))
    );
  },
  1000
);

function useUndo() {
  const [actions, setActions] = useState([]);

  const appendAction = useCallback((action) => {
    setActions((acts) => [...acts, action]);
  }, []);

  const undoAction = useCallback(() => {
    if (actions.length > 0) {
      const action = actions.pop();
      action();
      setActions([...actions]);
    }
  }, [actions]);

  return [actions.length, appendAction, undoAction];
}

export default function SessionDetail() {
  const { id } = useParams();
  const session = useSelector(sessionSelector(id));
  const { type, date, logs, duration, distance, place, linkPlace } =
    getSessionDetail(session);
  const dispatch = useDispatch();

  const [durationValue, setDurationValue] = useStateOrValue(duration);
  const [distanceValue, setDistanceValue] = useStateOrValue(distance || "0");
  const [placeValue, setPlaceValue] = useStateOrValue(place || "");
  const [linkPlaceValue, setLinkPlaceValue] = useStateOrValue(linkPlace || "");

  const [numberOfUndoActions, appendAction, undoAction] = useUndo();
  const { Icon, label } = sessions[type];

  const handleDateChange = useCallback(
    (date) => {
      dispatch(changeDate({ id, date: date.toISOString() }));
      appendAction(() => dispatch(setLogs({ id, logs })));
    },
    [dispatch, id, logs, appendAction]
  );

  const handleDurationChange = useCallback(
    (event) => {
      setDurationValue(event.target.value);
      setDuration(event.target.value, id, logs, dispatch, appendAction);
    },
    [dispatch, id, logs, appendAction, duration]
  );

  const handleDistanceChange = useCallback(
    (event) => {
      setDistanceValue(event.target.value);
      const value = Number(event.target.value);
      if (!isNaN(value)) {
        setDistance(value, id, distance, dispatch, appendAction);
      }
    },
    [dispatch, id, logs, appendAction, duration]
  );

  const handlePlaceChange = useCallback(
    (event, newValue) => {
      setPlaceValue(newValue);
      setPlace(newValue, id, place, dispatch, appendAction);
    },
    [dispatch, id, place, appendAction, duration]
  );

  const handleLinkPlaceChange = useCallback(
    (event) => {
      setLinkPlaceValue(event.target.value);
      setPlaceLink(event.target.value, id, linkPlace, dispatch, appendAction);
    },
    [dispatch, id, linkPlace, appendAction, duration]
  );

  if (!type || !Icon)
    return (
      <Screen>
        <Typography variant="subtitle1">No session found</Typography>
      </Screen>
    );

  return (
    <Screen>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          gap: 3,
        }}
      >
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box sx={{ maxWidth: "5em" }}>
            <Icon sx={{ width: 1, height: 1 }} />
          </Box>
          <Typography variant="h3">{label}</Typography>
        </Box>

        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            width: 1,
            gap: 3,
          }}
        >
          <DatePicker
            id="date"
            renderInput={(props) => <TextField {...props} />}
            label="Date"
            value={date}
            onChange={handleDateChange}
          />
          <TextField
            id="duration"
            label="Duration"
            value={durationValue}
            onChange={handleDurationChange}
          />
          {(type === "Running" || type === "Trekking") && (
            <TextField
              id="distance"
              label="Distance [Km]"
              value={distanceValue}
              onChange={handleDistanceChange}
            />
          )}
          <Autocomplete
            id="palce"
            options={[]}
            sx={{ width: "100%" }}
            freeSolo
            autoSelect
            renderInput={(params) => <TextField {...params} label="Place" />}
            value={placeValue}
            onChange={handlePlaceChange}
          />
          <TextField
            id="link"
            label="Link to place detail"
            value={linkPlaceValue}
            onChange={handleLinkPlaceChange}
          />

          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            {numberOfUndoActions > 0 && (
              <Button
                onClick={undoAction}
              >{`Undo last change (${numberOfUndoActions})`}</Button>
            )}
          </Box>
        </Box>
      </Box>
    </Screen>
  );
}
